* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-image: url("../82073184.5b388fda.png");
  background-size: 500px;
  background-attachment: fixed;
  margin: 0;
}

body p {
  font-family: Roboto Mono, monospace;
}

header {
  height: 8vh;
  z-index: 1;
  background-color: #000;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

nav img {
  height: 50px;
}

nav {
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  font-family: Pixelify Sans, sans-serif;
  display: flex;
}

h2, h3, h4 {
  font-family: Pixelify Sans, sans-serif;
}

nav div {
  align-items: center;
  display: flex;
}

header h1 {
  color: #fff;
  margin: 0;
  font-size: 1rem;
}

nav ul {
  display: none;
}

main {
  margin: 15vh 0;
}

.map {
  width: 80%;
  margin: auto;
  display: flex;
}

.button-container {
  width: 100%;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.menu-button {
  width: 250px;
  height: 60px;
  text-align: center;
  background-color: #fff;
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  margin: 10px;
  font-size: 1.2rem;
  display: flex;
}

.menu-button:hover {
  transition: all .3s;
  transform: scale(1.1);
}

footer {
  height: 10vh;
  z-index: 1;
  color: #fff;
  background-color: #000;
  flex-direction: column;
  align-items: center;
  padding-top: 15px;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

footer a {
  color: #fff;
}

footer p {
  margin: 0;
}

footer ul {
  width: 400px;
  justify-content: space-around;
  list-style: none;
  display: flex;
}

@media screen and (min-width: 768px) {
  header h1 {
    color: #fff;
    font-size: 3vh;
  }

  header .menu-icon {
    display: none;
  }

  nav ul {
    color: #fff;
    width: 70%;
    justify-content: space-around;
    font-size: 1.5rem;
    list-style: none;
    display: flex;
  }

  header {
    height: 15vh;
  }

  nav {
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
    display: flex;
  }

  nav img {
    height: 50px;
  }

  nav a {
    color: #fff;
    text-decoration: none;
  }

  nav li:hover {
    transition: all 1s;
    transform: scale(1.5);
  }

  nav a:hover {
    color: #e19200;
  }
}

.transparent {
  width: 100%;
  height: 10vh;
  border: 1px solid red;
}

.socialmedia {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
}

.socialmedia a {
  text-align: center;
  color: #fff;
  padding: 16px;
  font-size: 25px;
  display: block;
}

.socialmedia a:hover {
  background: #000;
}

.whatsapp {
  color: #fff;
  background: #01cb37;
}

.instagram {
  color: #fff;
  filter: progid:DXImageTransform. Microsoft. gradient( startColorstr= "#f09433", endColorstr= "#bc1888", GradientType= 1 );
  background: linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
}

.facebook {
  color: #fff;
  background: #3b5998;
}

.twitter {
  color: #fff;
  background: #007bb5;
}

.youtube {
  color: #fff;
  background: #b00;
}

.orderhere {
  height: 75px;
  width: 75px;
  color: #fff;
  background-color: #0dc600;
  border: 3px solid #fff;
  font-size: 1em;
  font-weight: 700;
  position: fixed;
  bottom: 45vh;
  right: 0;
}

.orderhere:hover {
  transition: all 1s;
  transform: scale(1.5)translateX(-20px);
}

@media (max-width: 768px) {
  .socialmedia a {
    text-align: center;
    color: #fff;
    padding: 12px;
    font-size: 20px;
    transition: all .3s;
    display: block;
  }

  .orderhere {
    display: none;
  }
}

